//    
import { createSlice } from '@reduxjs/toolkit';

//      
import authOperations from './authOperations';

//storage  persistReducer         localStorage
import storage from 'redux-persist/lib/storage';
import { persistReducer } from 'redux-persist';

//  
const initialState = {
  user: { username: '', email: '' },
  token: null,
  isLoggedIn: false,
};

//  
//         ()
const authSlise = createSlice({
  name: 'auth',
  initialState,
  extraReducers: builder => {
    builder
      //    builder

      //Log-In
      .addCase(authOperations.logIn.fulfilled, (state, action) => {
        state.user = action.payload.user;
        state.token = action.payload.token;
        state.isLoggedIn = true;
      })
      //Log-Out
      .addCase(authOperations.logOut.fulfilled, (state, action) => {
        state.user = { name: '', email: '' };
        state.token = '';
        state.isLoggedIn = false;
      })
      //set Current User
      .addCase(authOperations.fetchCurrentUser.fulfilled, (state, action) => {
        state.user = action.payload;
        state.isLoggedIn = true;
      })
      // PATCH Current User
      .addCase(authOperations.patchCurrentUser.fulfilled, (state, action) => {
        state.user = action.payload;
      });
  },
});

// 
const authReducer = authSlise.reducer;

//  
const persistConfig = {
  key: 'auth',
  storage,
  whitelist: ['token'],
};

//     (    )
export const persistedAuthReducer = persistReducer(persistConfig, authReducer);
